.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px #36454F;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}

.image {
    border-radius: 75%;
    width: 100px;
    height: 100px;
}

.list {
    margin-right: 5%;
}